import 'webpack-jquery-ui/slide-effect'; 
import $ from 'jquery';

const jqueryEffects = (() => {
    const serviceSlideAuto = () => {
        setTimeout(() => { 
            $(".first-slide").hide("slide", { direction: "right" }, 1000);
        }, 10000);
        setTimeout(() => {
            $('.second-slide').show("slide", { direction: "left" }, 1000);
        },11000);
    }

    const serviceSlideLeft = () => {
        $(".second-slide").hide("slide", { direction: "left" }, 1000);
        setTimeout(() => {
            $('.first-slide').show("slide", { direction: "right" }, 1000);
        },1001)
    }
    
    const serviceSlideRight= () => {
        $(".first-slide").hide("slide", { direction: "right" }, 1000);
        setTimeout(() => {
            $('.second-slide').show("slide", { direction: "left" }, 1000);
        },1000);
    }

    const galleryMouseOverEvent = () => {
        function slideUp(e) {
            let info = $(this).find(".caption-cover");
            info.addClass('hover-active')
        }
        
        function slideDown(e) {
            $(this).find(".caption-cover").removeClass('hover-active')
        }
        
        $( ".landing-gallery" ).delegate( ".image-container", "mouseenter mouseleave", function(e){
            if( e.type === "mouseenter" ) {
             slideUp.call( this, e );   
            }
            else {
             slideDown.call( this, e );   
            }
        });
    }
    
    return { serviceSlideLeft, serviceSlideAuto, serviceSlideRight, galleryMouseOverEvent}
})();

export default jqueryEffects