import jqueryEffects from './jqueryeffects.js';

const domEvent = (() => {
    const indicatorClick = () => {
        const indicatorOne = document.querySelector('.indicator-one');
        const indicatorTwo = document.querySelector('.indicator-two'); 

        indicatorOne.addEventListener('click', () => {
            if (indicatorOne.classList.contains('active')){
                return
            } else {
                indicatorOne.classList.add('active');
                indicatorTwo.classList.remove('active')
                jqueryEffects.serviceSlideLeft(); 
            }
        })

        indicatorTwo.addEventListener('click', () => {
            if (indicatorTwo.classList.contains('active')){
                return
            } else {
                indicatorTwo.classList.add('active');
                indicatorOne.classList.remove('active')
                jqueryEffects.serviceSlideRight(); 
            }
        })
    };

    
    const imageCaptionOnClick = (e) => {
        const imageContainers = document.querySelectorAll('.image-container');

        for (let container of imageContainers){
            container.addEventListener('click', (e) => {
                e.currentTarget.firstChild.firstChild.firstChild.click();
            })
        }
    }

    
    return { indicatorClick, imageCaptionOnClick }
})();

export default domEvent