import React, { useEffect } from 'react';
import { SRLWrapper } from 'simple-react-lightbox';
import projectsImports from '../imports/projectsImports.js';
import domEvent from '../js/domevents.js'
import jqueryEffects from '../js/jqueryeffects'
import '../css/landing.css'

const Projects = () => {
    useEffect(() => {
        domEvent.imageCaptionOnClick()
        jqueryEffects.galleryMouseOverEvent();
        window.scrollTo(0, 0);
    });
    const renderProjectsGallery = () => {
        return (
            <SRLWrapper>
                <div className="landing-gallery">
                    {
                        projectsImports.map(project => (                 
                            <figure className="image-container" >
                                <div className="inner-image"  style={{backgroundImage: `url(${project.mainImg})`}}>       
                                    {project.lightbox.map((img, index)=> (
                                        index === 0 ? (
                                            <a href="/"><img src={img.src} alt="" style={{opacity: 0}} /></a>
                                        ) : (
                                            <a href="/"><img class="d-none" src={img.src} alt="" /></a>
                                        )   
                                    ))}
                                    <figcaption className="caption-cover">
                                        <p>{project.caption}</p>
                                    </figcaption>
                                </div>
                            </figure>        
                        
                        ))
                    }
                </div>
            </SRLWrapper>
        )
    }
    return (
        <div className="gallery-page">
        <div>
            <div className="gallery-head-bg px-3">
                <header className="page-head">
                    <h1>Gallery</h1>
                    <div className="ph-break-line"></div>
                    <p>We are proud to have been involved in the successful delivery of many projects over the years, <br className="d-none d-md-block"/> our projects are designed by you and tailored to your needs</p> 
                </header>
                
            </div>
            </div>
            <div className="bg-blue-4 landing-section">
                {renderProjectsGallery()}
            </div>
            
        </div>
    )
}

export default Projects